import React, { useEffect, Suspense, lazy } from 'react';
import { HashRouter as Router, Routes, Route, Navigate, useLocation } from 'react-router-dom';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { AuthProvider, useAuth } from './context/AuthContext';

// Lazy loading components
const StudentLogin = lazy(() => import('./pages/student/Login'));
const StudentHome = lazy(() => import('./pages/student/Home'));
const StudentSubjectDetail = lazy(() => import('./pages/student/SubjectDetail'));
const Exam = lazy(() => import('./pages/student/Exam'));
const ExamResult = lazy(() => import('./pages/student/ExamResult'));
const AdminLogin = lazy(() => import('./pages/admin/AdminLogin'));
const AdminDashboard = lazy(() => import('./pages/admin/Dashboard'));
const Terms = lazy(() => import('./pages/admin/Terms'));
const Subjects = lazy(() => import('./pages/admin/Subjects'));
const AddSubject = lazy(() => import('./pages/admin/AddSubject'));
const SubjectDetail = lazy(() => import('./pages/admin/SubjectDetail'));
const AddExam = lazy(() => import('./pages/admin/AddExam'));
const ManageQuestions = lazy(() => import('./pages/admin/ManageQuestions'));
const Students = lazy(() => import('./pages/admin/Students'));
const StudentQuestions = lazy(() => import('./pages/admin/StudentQuestions'));

// Error Boundary simple fallback
const LoadingFallback = () => (
  <div className="loading-container">
    <div className="loader"></div>
    <p> ...</p>
  </div>
);

//   (  )
const ProtectedRoute = ({ children, adminOnly = false }) => {
  const { isAuthenticated, isAdminAuthenticated, loading } = useAuth();

  if (loading) {
    return <div className="loading"> ...</div>;
  }

  if (adminOnly) {
    return isAdminAuthenticated ? children : <Navigate to="/admin/login" />;
  }

  return isAuthenticated ? children : <Navigate to="/login" />;
};

function AppContent() {
  const location = useLocation();

  useEffect(() => {
    // Handle potential GitHub Pages redirect or direct URL access
    const query = new URLSearchParams(location.search);
    const redirectPath = query.get('/');
    if (redirectPath) {
      console.log('Redirecting to hash path:', redirectPath);
      window.location.replace('/#' + redirectPath);
    }
  }, [location]);

  useEffect(() => {
    //    (Right Click)
    const handleContextMenu = (e) => {
      e.preventDefault();
    };

    //  
    const handleCopy = (e) => {
      e.preventDefault();
    };

    //     (Ctrl+C, Ctrl+V, Ctrl+U, F12, etc.)
    const handleKeyDown = (e) => {
      // Ctrl+C, Ctrl+V, Ctrl+U (view source), Ctrl+Shift+I (inspect)
      if (
        (e.ctrlKey && (e.keyCode === 67 || e.keyCode === 86 || e.keyCode === 85 || (e.shiftKey && e.keyCode === 73))) ||
        e.keyCode === 123 // F12
      ) {
        e.preventDefault();
      }
    };

    document.addEventListener('contextmenu', handleContextMenu);
    document.addEventListener('copy', handleCopy);
    document.addEventListener('keydown', handleKeyDown);

    return () => {
      document.removeEventListener('contextmenu', handleContextMenu);
      document.removeEventListener('copy', handleCopy);
      document.removeEventListener('keydown', handleKeyDown);
    };
  }, []);

  return (
    <>
      <ToastContainer
        position="top-center"
        autoClose={3000}
        hideProgressBar={false}
        newestOnTop
        closeOnClick
        rtl
        pauseOnFocusLoss
        draggable
        pauseOnHover
      />
      
      <Suspense fallback={<LoadingFallback />}>
        <Routes>
          {/*     */}
          <Route path="/login" element={<StudentLogin />} />
          
          {/*    */}
          <Route path="/" element={<ProtectedRoute><StudentHome /></ProtectedRoute>} />
          <Route path="/subject/:id" element={<ProtectedRoute><StudentSubjectDetail /></ProtectedRoute>} />
          <Route path="/exam/:id" element={<ProtectedRoute><Exam /></ProtectedRoute>} />
          <Route path="/exam-result/:id" element={<ProtectedRoute><ExamResult /></ProtectedRoute>} />

          {/*    */}
          <Route path="/admin/login" element={<AdminLogin />} />
          <Route path="/admin" element={<ProtectedRoute adminOnly><AdminDashboard /></ProtectedRoute>} />
          <Route path="/admin/terms" element={<ProtectedRoute adminOnly><Terms /></ProtectedRoute>} />
          <Route path="/admin/subjects" element={<ProtectedRoute adminOnly><Subjects /></ProtectedRoute>} />
          <Route path="/admin/add-subject" element={<ProtectedRoute adminOnly><AddSubject /></ProtectedRoute>} />
          <Route path="/admin/subject/:id" element={<ProtectedRoute adminOnly><SubjectDetail /></ProtectedRoute>} />
          <Route path="/admin/subject/:id/add-exam" element={<ProtectedRoute adminOnly><AddExam /></ProtectedRoute>} />
          <Route path="/admin/exam/:id/questions" element={<ProtectedRoute adminOnly><ManageQuestions /></ProtectedRoute>} />
          <Route path="/admin/students" element={<ProtectedRoute adminOnly><Students /></ProtectedRoute>} />
          <Route path="/admin/questions" element={<ProtectedRoute adminOnly><StudentQuestions /></ProtectedRoute>} />

          {/*    */}
          <Route path="*" element={<Navigate to="/" />} />
        </Routes>
      </Suspense>
    </>
  );
}

function App() {
  return (
    <Router>
      <AuthProvider>
        <AppContent />
      </AuthProvider>
    </Router>
  );
}

export default App;